import React, { useEffect, useState } from "react";
import axios from "axios";
import MaterialTable from "material-table";
import appConfig from "../../config/appConfig";
import { connect } from "react-redux";
import alertify from "alertifyjs";

function TransactionRequestList(props) {
  const [transactions, setTransactions] = useState([]);
  const getTransactions = () => {
    axios
      .get(appConfig.baseApiUrl + "transaction/GetTransactionRequests")
      .then((resJson) => {
        setTransactions(resJson.data);
      });
  };

  useEffect(() => {
    getTransactions();
  }, []);

  return (
    <div>
      <MaterialTable
        title="Talep Edilen lem Listesi"
        columns={[
          { title: "lem Tipi", field: "tranSactionType" },
          { title: "Mteri Ad", field: "customerName" },
          { title: "Tutar", field: "amount" },
          { title: "Giri Hesab", field: "toAccount" },
          { title: "k Hesab", field: "fromAccount" },
          { title: "Al Kuru", field: "buyingRate" },
          { title: "Sat Kuru", field: "sellingRate" },
          { title: "Stat", field: "status" },
          { title: "Aan Kullanc", field: "createdUser" },
          { title: "ilem Tarihi", field: "transactionDate", type: "date" },
          //   { title: "Al Tarihi", field: "createdDate" },
        ]}
        data={transactions}
        actions={[
          {
            icon: "check",
            tooltip: "Onayla",
            onClick: (event, rowData) => {
              alertify.confirm(
                "Onay",
                "Tm Bilgiler Silinecektir onaylyor musunuz?",
                function () {
                  axios
                    .post(
                      appConfig.baseApiUrl +
                        "transaction/ApproveTransactionRequest",
                      rowData
                    )
                    .then((resJson) => {
                      setTransactions(resJson.data);
                      alertify.success("lem Onayland!");
                    });
                },
                function () {
                  alertify.warning("ptal Edildi");
                }
              );
            },
          },
          {
            icon: "clear",
            tooltip: "Reddet",
            onClick: (event, rowData) => {
              alertify.confirm(
                "Onay",
                "Tm Bilgiler Silinecektir onaylyor musunuz?",
                function () {
                  axios
                    .post(
                      appConfig.baseApiUrl +
                        "transaction/RejectTransactionRequest",
                      rowData
                    )
                    .then((resJson) => {
                      setTransactions(resJson.data);
                      alertify.error("lem Reddedildi!");
                    });
                },
                function () {
                  alertify.warning("ptal Edildi");
                }
              );
            },
          },
        ]}
        options={{
          actionsColumnIndex: -1,
          exportButton: true,
          pageSize: 10,
        }}
        localization={{
          pagination: {
            labelDisplayedRows: "{from}-{to} of {count}",
          },
          header: {
            actions: "lemler",
          },
          body: {
            emptyDataSourceMessage: "No records to display",
            filterRow: {
              filterTooltip: "Filter",
            },
          },
          toolbar: {
            exportTitle: "Dosyaya kar",
            searchTooltip: "Ara",
            searchPlaceholder: "Aranacak kelimeyi giriniz",
          },
        }}
      />
    </div>
  );
}

function mapStateToProps(state) {
  return {
    isLoading: state.loadingReducer,
    totalRequest: state.totalRequestReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: {},
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TransactionRequestList);
