import React from "react";
import { FormGroup, Button, Grid } from "@material-ui/core";
import axios from "axios";
import alertify from "alertifyjs";
import appConfig from "../../config/appConfig";

export default function ResetData(props) {
  const resetAllData = () => {
    alertify.confirm(
      "Onay",
      "Tm Bilgiler Silinecektir onaylyor musunuz?",
      function () {
        axios
          .post(appConfig.baseApiUrl + "settings/DeleteAllData")
          .then((resJson) => {
            alertify.success("lem Baarl");
          });
      },
      function () {
        alertify.error("ptal Edildi");
      }
    );
  };

  return (
    <div>
      <Grid container>
        <FormGroup>
          <Button color="primary" onClick={() => resetAllData()}>
            Tm Bilgileri Sfrla{" "}
          </Button>
        </FormGroup>
      </Grid>
    </div>
  );
}
